@import "../../css-config/mixins.scss";

.main-slider{
    padding: 30px 40px;

    .content{

        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 10px;

        .image-slider{
            width: 100px;
            height: 100px;
            border-radius: 100%;
            border: 1px solid black;
            padding: 6px;
        }

        .heading{
            font-size: 17px;
            color: brown;
        }
        
    }

   
}

